.timer {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.timer__btn {
  align-self: center;
  min-width: 150px;
  min-height: 30px;
  margin: 5px 0;
}
.timer__input {
  padding: 10px ;
}
input:invalid {
  border: 3px solid red;
}
.timer__number {
  color: black;
  font-size: 20px;
}