*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
ol,
li {
  list-style: none;
}
h1,h2,h3,h4,h5,h6 {
  font-weight: inherit;
  font-size: inherit;
}
html,body {
  height: 100%;
  line-height: 1;
  font-size: 14px;
  font-weight: 500;
  color: #FFFF;
  background-color: aqua;
}
button {
  cursor: pointer;
  display: block;
  border-radius: 25px;
  color: #FFFF;
  padding: 5px 20px;
  background-color: #252b42;
}
.wrapper {
  max-width: 1260px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}
.timer__number {
  font-size: 30px;
  color: #252b42;
  margin: 0 0 10px 0;
}